@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900);
html,
body {
  padding: 0;
  margin: 0;
  font-family: 'Roboto', sans-serif;
  font-size: 18px; }

body {
  overflow-x: hidden; }

h1 {
  font-size: 1.8rem;
  padding: 0 0 0.5em 0;
  position: relative;
  display: inline-block; }
  h1::after {
    border-bottom: 3px solid #f03c3c;
    content: "";
    left: 0;
    bottom: 0;
    width: 50%;
    position: absolute; }

h2 {
  font-size: 1.6rem;
  display: inline-block; }

a {
  text-decoration: none; }

h3 {
  margin-top: 0;
  margin-bottom: 0.5em; }

@media (min-width: 1500px) and (max-width: 1800px) {
  html {
    font-size: 16px; } }

@media (min-width: 1300px) and (max-width: 1500px) {
  html {
    font-size: 14px; } }

@media (min-width: 900px) and (max-width: 1300px) {
  html {
    font-size: 12.5px; } }

.nav {
  display: -webkit-flex;
  display: flex;
  padding: 1.5em 7vw;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  color: white;
  transition: 0.4s ease all;
  z-index: 1000; }
  .nav .nav_brand {
    font-size: 1.25rem;
    font-weight: 600; }
  .nav .nav_items {
    padding: 0 0 0.25em;
    font-size: 1.2rem;
    cursor: pointer;
    font-weight: 500;
    margin: 0 0 0 2.5em; }
  .nav .nav_array_active {
    position: relative; }
    .nav .nav_array_active::after {
      position: absolute;
      border-bottom: 2px solid white;
      bottom: 0;
      left: 15%;
      width: 70%;
      content: " "; }

.scroll {
  background: white;
  color: black;
  padding: 1em 7vw;
  box-shadow: 0px 2px 6px rgba(110, 110, 110, 0.34); }
  .scroll .nav_array_active::after {
    border-bottom-color: black; }

.home {
  height: 100vh;
  width: 100vw;
  display: grid;
  -webkit-align-items: center;
          align-items: center;
  justify-items: center;
  color: white; }
  .home .overlay {
    position: fixed;
    z-index: -100;
    background-image: url(/static/media/background.78208b0f.jpg);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100vh;
    width: 100vw;
    background-size: cover;
    background-position: center; }
    .home .overlay span {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: black;
      opacity: 0.8;
      height: 100%;
      width: 100%; }
  .home .home_name {
    text-align: center;
    line-height: 1.4em;
    font-size: 5rem;
    font-weight: 500; }
  .home .home_det {
    text-align: center;
    font-size: 1.5rem;
    font-weight: 300; }
  .home .home_abt {
    text-align: center;
    margin-top: 5em; }
    .home .home_abt a {
      font-size: 1.25em;
      padding: 1rem 2rem;
      border: 0.125rem solid white;
      border-radius: 0.25rem;
      cursor: pointer; }

.projects {
  background: white;
  padding: 4.5rem 7.2vw; }
  .projects .project_unit .project_header {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between; }
  .projects .project_unit .project_link {
    display: inline-block; }
    .projects .project_unit .project_link a {
      color: blue; }
  .projects .project_unit .project_details {
    display: grid;
    grid-column-gap: 4vw;
    grid-template-columns: 40vw 40vw; }
    .projects .project_unit .project_details .project_image img {
      width: 100%; }
    .projects .project_unit .project_details .project_details_description {
      text-align: justify;
      opacity: 0.5;
      font-size: 1.1rem; }
    .projects .project_unit .project_details .project_details_stack {
      padding-bottom: 0.5em;
      display: grid;
      grid-template-columns: 20% auto; }
      .projects .project_unit .project_details .project_details_stack span:first-child {
        font-weight: bold; }
      .projects .project_unit .project_details .project_details_stack span:last-child {
        opacity: 0.5; }
  .projects .project_margin {
    margin-top: 5rem; }

.contact {
  background: white;
  padding: 4.5rem 7.2vw;
  font-size: 1.2rem; }
  .contact .contact_unit {
    color: black;
    font-size: 0.9em;
    margin-top: 1em; }
    .contact .contact_unit a {
      color: black; }
  .contact .contact_array {
    display: grid;
    grid-template-columns: auto auto;
    padding-top: 1rem; }
    .contact .contact_array span {
      font-weight: 500;
      display: block; }
    .contact .contact_array .contact_unit {
      line-height: 140%; }

.credits {
  background: white;
  padding: 0.7rem 7.2vw;
  text-align: center; }
  .credits i {
    color: #6F4E37; }

.about {
  background: #F5F5F5;
  padding: 4.5rem 7.2vw; }

.work_array {
  margin-top: 1em;
  display: -webkit-flex;
  display: flex; }
  .work_array .work_unit {
    background: white;
    padding: 36px;
    border-radius: 0.3125rem;
    box-shadow: 0rem 0.1875rem 2.5rem rgba(100, 103, 105, 0.12);
    width: 15em;
    margin-right: 17vw; }
    .work_array .work_unit .work_title {
      font-weight: 500;
      font-size: 1.5em;
      text-align: center; }
    .work_array .work_unit ul {
      margin: 0;
      padding: 0;
      margin-top: 1.5em;
      list-style-type: none;
      text-align: center; }
      .work_array .work_unit ul li {
        margin-top: 0.5em; }
  .work_array .work_unit:last-child {
    margin-right: 0vw; }

.about_resume {
  padding-top: 4rem;
  text-align: center; }
  .about_resume a {
    padding: 1rem 2rem;
    background: rgba(241, 79, 79, 0.8);
    color: snow;
    border-radius: 0.25rem;
    font-weight: 700;
    font-size: 1.2rem;
    transition: 0.2 ease all; }
  .about_resume a:hover {
    background: #f14f4f; }

.about_desc {
  line-height: 140%; }

